.wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 3px 20px 3px 20px;
}

.text {
  flex-grow: 1;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 4px;
  background-color: transparent;
  cursor: pointer;
  color: #f44336;
}



.saved {
  color: rgb(212, 202, 55);
}


.btn:hover {
  background-color: #e2e5e8;
}

.btn:active {
  background-color: #bdbdbd;
}

.checkbox {
  width: 24px;
  height: 24px;
  cursor: pointer;
}
